.App-header {
  background-color: #000000;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.Parallax {
  height: 100vh;
  width: 100%;
  overflow-x: hidden;
  overflow-y: visible;
  
  &_Group {
    position: relative;
    height: 100vh;
    transform-style: preserve-3d;
  }

  &_BG {
    position:fixed;
    top:0px;
    margin:auto;
    width:100%;
    min-height:400px;
    top: 0; left: 0;
    background-position: top center;
    background-repeat: no-repeat;
  }

  &_BG1 {
    background-image: url("./Images/Layer1.png");
    z-index: 1;
  }
  &_BG2 {
    background-image: url("./Images/Layer2.png");
    z-index: 2;
  }
  &_BG3 {
    background-image: url("./Images/Layer3.png");
    z-index: 3;
  }
  &_BG4 {
    background-image: url("./Images/Layer4.png");
    z-index: 5;
  }
  &_TXT {
    background-image: url("./Images/Layer5.png");
    z-index: 4;
  }
}

.bg {
  height: 120vh;
  min-height: 69vw;
  width: 100%;
  position: relative;
  background-size: cover;
  background-position: center top;
  background-image: url("./Images/Personal_Website_BGonly.jpg");
}

.myname {
  height: 112vh;
  min-height: 60vw;
  width: 100%;
  position: relative;
  background-size: cover;
  background-position: center top;
  background-image: url("./Images/Layer5.png");
}

.sectionheader {
  font-size: 5vh;
  text-align: center;
  padding-top: 5vh;
  padding-bottom: 5vh;
  color: #f0c26c;
}

.exp {
  &_container {
    padding-left: 8vw;
    padding-right: 8vw;
    display: flex; 
    flex-direction: row;
    padding-bottom: 10vh;
  }
  &_image {
    border-radius: 50%;
    height: 18vh;
    margin-right: 5vw;
  }
  &_name {
    font-size: 3vh;
    font-weight: bold;
    color: #eee3cf;
  }
  &_position {
    font-size: 2.5vh;
    font-weight: bold;
    color: #efc0b7;
  }
  &_duration {
    font-size: 2.2vh;
    font-weight: bold;
    font-style: italic;
    color: #b7efef;
  }
  &_desc {
    padding-top: 1vh;
    font-size: 2vh;
    color: #dbdbdb;
  }
}

.icon {
  &black {
    width: 3vh;
    padding-left: calc(0.3% + 0.5vh);
    padding-right: calc(0.3% + 0.5vh);
  }
  &sblack {
    top: 34%;
    left: 58%;
    width: 100%;
    height: 100%;
    position: absolute;
    text-align: left;
  }
  &white {
    width: calc(10px + 2vw);
    padding-left: 2vw;
    padding-right: 2vw;
  }
  &swhite {
    width: 100%;
    text-align: center;
    padding-bottom: 10vh;
  }
}